.Navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 48px;
  height: 64px;
}

.left {
  visibility: hidden;
  flex-grow: 1;
}

.middle {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-grow: 2;
}

.right {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-grow: 1;
}

.logo_wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 186px;
  min-width: 100%;
  width: 100%;
}

.back_logo {
  height: 100%;
}

.main_logo {
  position: absolute;
  width: 100%;
  height: 140px;
  transition: 0.2s;
}

.main_logo:hover {
  height: 160px;
  transition: 0.5s;
}

.TabButton {
  padding: 0px 16px;
  height: 24px;
  width: auto;
}

.ShopButton {
  height: 48px;
  width: auto;
  animation: hover 6s infinite;
  animation-direction: normal;
}

@keyframes hover {
  0% {
    transform: translate(-4px, -34px);
  }
  50% {
    transform: translate(4px, -34px);
  }
  100% {
    transform: translate(-4px, -34px);
  }
}
